import styled, { useTheme } from "styled-components";
import { SearchIcon } from "../icons";
import type { IButtonSearch } from "./logo.d";

export const ButtonSearch = (p: IButtonSearch) => {
  const { colors } = useTheme();
  return (
    <IconWrapper color={colors.primary} {...p}>
      <ButtonIcon />
    </IconWrapper>
  );
};
const ButtonIcon = styled(SearchIcon)({});
const IconWrapper = styled.div(
  ({ size = 32, color }: { size?: number; color: string }) => ({
    borderRadius: "50%",
    width: size,
    height: size,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    color,
    ":hover": {
      backgroundColor: "rgba(0, 0, 0, 0.1)",
    },
    svg: {
      width: size * 0.5,
      height: size * 0.5,
    },
  })
);
